import {DoPay} from 'api/user'
export default{
	data(){
		return {
			baseUrl:this.$store.state.SRC+'/config/app',
			list:[20,30,50,80,100],
			moeny:0,
			value:'',
			state:{}
		}
	},
	methods:{
		open(url){
			uni.navigateTo({
				url:url
			})
		},
		pay(val){
			this.money = val
			if(this.money){
				DoPay({
					rechargemoney: this.money,
					payid: 15,//微信支付id：15
				}).then(res=>{
					if (res.result) {
						uni.navigateTo({
							url: '/pages/payment/payment?orderid=' + res.orderid + '&ordertype=' + res.ordertype
						})
					
					} else {
					     uni.showToast({
					     	title:res.msg,
							icon:'none'
					     })
					}
				})
			}
			
		},
		change(){
			this.money = this.value
			if(this.money){
				DoPay({
					rechargemoney: this.money,
					payid: 15,//微信支付id：15
				}).then(res=>{
					if (res.result) {
					
						 uni.navigateTo({
						 	url: '/pages/payment/payment?orderid=' + res.orderid + '&ordertype=' + res.ordertype
						 })
					} else {
					     uni.showToast({
					     	title:res.msg,
							icon:'none'
					     })
					}
				})
			}
		},	
	},
	onLoad() {
		this.$store.ready(state=>{
			this.state = state
		})
	}
}
